.card {
  min-width: 300px;
  min-height: 80px;
  margin: 10px;
  box-shadow:
  0px 1px 3px 0px rgba(0,0,0,0.2),
  0px 1px 1px 0px rgba(0,0,0,0.14),
  0px 2px 1px -1px rgba(0,0,0,0.12);
}

.form{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.swanky-input-container{
  position: relative;
}

.swanky-input-container input{
  position: absolute;
  bottom: 2px;
  right: 2px;
  border:none;
  border-bottom: 1px solid black;
  width: calc(100% - 14px);
  height: 60px;
  font-size: 22px;
  padding-left: 10px ;
  outline: none;
  transition: font-size 0.25s , color 1s;
}

.swanky-input-container input:focus {
  font-size: 26px;
  border-bottom: 2px solid green;
}


.swanky-input-container span.title{
  position: absolute;
  top: 5px;
  left: 5px;
  cursor: pointer;
  font-weight: 300;
  font-size: 16px;
  user-select: none;

  z-index: 15;
  transition: font-size 0.25s, color 1s;

}

.swanky-input-container input:focus + span.title,
.swanky-input-container:hover span.title {

  color:green;
  font-size: 12px;

}

.album-dropdown-base img  , ul.album-menu img{
  height: 45px;
  width: 45px;

}

.album-dropdown-base, ul.album-dropdown-base li{
  display: flex;
  align-items: center;
  justify-content :space-between;
  cursor: pointer;
  padding: 30px 25px 10px 10px;

}

ul.album-menu li span, .album-dropdown-base span:not(.dropdown-arrow){
  display : inline-block;
  width :180px;
  text-overflow: ellipsis;
  white-space: space nowrap;
  overflow: hidden;
}


ul.album-menu li:hover{
  background-color: #eee;

}

ul.album-menu li span :nth-child(2), .album-dropdown-base span:nth-child(2){
  width: 38px;
  padding: 0 3px 0 9px;
}

.swanky-input-container .react-datepicker-popper{
  z-index: 40;
}

.swanky-input-container .react-datepicker{
  transform:scale(1.25, 1.25);
}

.swanky-input-container .react-datepicker__input-container{
  position: static;
}

.album-dropdown-base img{
  height: 45px;
  width: 45px;
}

.album-dropdown-base .dropdown-arrow{
  position: absolute;
  right: 5px;
  user-select: none;
}

ul.album-menu{
  list-style: none;
  padding: 0;

  position : absolute;
  top:100%;

  max-height: 25vh;
  overflow-y: auto;

  margin: 2px 0 0 0;


  z-index: 30;
  background-color: white;
}


.done-container {
  flex-basis: 100%;
  display: flex;
  justify-content: center;
}

.done-button {
  background-color: grey;
  padding: 15px;
  margin: 10px 0;
  font-size: 24px;
  border-radius: 5px;
  user-select: none;
}

.done-button:hoover{
  background-color: #eee;
}

.modal-open{
  position:fixed;
  top:20vh;
  left: 20vw;
  width: 60vw;
  height: 40vh;

  background-color: #ccc;
  border-radius: 5px;
  padding: 8px;
  z-index: 1000;


  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}


.modal-open .x-button{

  fill:white;
  stroke: black;
  stroke-width: 3px;

  width: 20px;
  height: 20px;

  position: absolute;
  top: 8px;
  right: 8px;
}


.modal-open .x-button circle, .modal-open path{
  cursor : pointer;
}


.modal-open .x-button:hover path{
  stroke:red;


}


.modal-closed{
  display: none;
}


.modal-shade{
  position: fixed;
  width: 100%;
  height: 100%;

  top:0;
  left: 0;

  z-index: 420;
  background: #3338;
}






.fake-button{
  border: 2px outset #ccc;
}

.fake-button:active span{
  display: block;
  transform: translate(2px ,2px);
}
.fake-button:active span:first-child{
  color: blue;
}
.fake-button:active span:last-child{
  color: red;
}
.email-invalid{
  color: red;
  position: absolute;
  top:5px;
  right: 5px;

  text-align: right;
  font-size:  12px;

}

.swanky-input-container input:focus ~ span.email-invalid {

  display: none;

}

.goldRecords{
  position: absolute;
  right: 50px;
  height: 100%;

  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.goldRecord{
  width: 20px;
}

.goldRecord img{
  height: 50px;
  width: 50px
}